export default {
  name: "preview-page",
  props: {
    videoUrl: String,
    showVideo: Boolean,
    pageData: {
      type: Object,
      require: true,
      default: () => {
        return { width: 0, height: 0 };
      }
    }
  },
  data() {
    return {
      style() {
        const { width, height } = this.pageData;

        return {
          width: width + "px",
          height: height + "px",
          "margin-left": -width / 2 + "px",
          "margin-top": -height / 2 + "px"
        };
      }
    };
  },
  computed: {
    $video() {
      return this.$refs.vueMiniPlayer.$video;
    }
  },
  methods: {
    closePanel() {
      this.$emit("closePanel", false);
    },

    getVideo() {
      return {
        url: this.videoUrl,
        muted: false,
        loop: false,
        preload: "auto",
        volume: 1,
        autoplay: false
      };
    }
  }
};
