import { Form, FormItem, Dialog, Button, Select, Option, Tag } from "element-ui";

export default {
  components: {
    [Tag.name]: Tag,
    [Select.name]: Select,
    [Option.name]: Option,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Dialog.name]: Dialog,
    [Button.name]: Button
  },
  data() {
    return {
      userHeadImage: require("@/assets/images/headerImage.png"),
      pageId: "",
      visible: false,
      callback: null,
      loading_user: false,
      groupInput: "",
      groupOptions: [],
      userInput: [],
      userOptions: [],
      cooperationUserList: []
    };
  },
  created() {
    this.getCooperationList();
  },
  methods: {
    getCooperationList() {
      this.$API.getCooperationUserListByPageId({ pageId: this.pageId }).then(res => {
        this.cooperationUserList = res.body || [];
      });
    },
    remoteMethod_user(str) {
      this.loading_user = true;
      this.$API
        .getUserListByKeywords({ keywords: str })
        .then(res => {
          this.userOptions = res.body || [];
          this.loading_user = false;
        })
        .catch(() => {
          this.loading_user = true;
        });
    },
    addUser() {
      if (!this.userInput.length) return;
      this.$API.addCooperation({ pageId: this.pageId, userIds: this.userInput }).then(res => {
        this.cooperationUserList = res.body || [];
        this.userInput = [];
      });
    },
    // 删除
    delUser(userId, index) {
      this.$API.delCooperation({ pageId: this.pageId, userId: userId }).then(() => {
        this.cooperationUserList.splice(index, 1);
      });
    }
  }
};
