import { Dropdown, DropdownMenu, DropdownItem } from "element-ui";
import editorProjectConfig from "@/pages/editor/DataModel";
import addCooperationer from "@/components/add-cooperationer/index.js";

export default {
  props: {
    showPublishState: {
      type: Boolean,
      default: true
    },
    showMoreBtn: {
      type: Boolean,
      default: true
    },
    pageType: {
      type: String,
      default: "h5"
    },
    pageData: Object,
    // 操作按钮显示哪些按钮  根据type来匹配。
    btnList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },

  components: {
    [Dropdown.name]: Dropdown,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem
  },

  data() {
    return {
      loading: false,
      defaultCoverImage: require("@/assets/images/pagecover.jpg"),
      operationDataList: [
        {
          title: "设为我的模板",
          eventType: "setTemplate",
          iconClass: ""
        },
        {
          title: "协作设置",
          eventType: "cooperation",
          iconClass: ""
        },
        {
          title: "删除",
          eventType: "delete",
          extraClassName: "error",
          iconClass: ""
        },
        {
          title: "退出协作",
          eventType: "unCooperation",
          extraClassName: "error",
          iconClass: ""
        }
      ]
    };
  },

  methods: {
    command(type) {
      switch (type) {
        case "publish":
          this.publish();
          break;

        case "copyUrl":
          this.copyUrl();
          break;

        case "setTemplate":
          this.setTemplate();
          break;

        case "viewPageData":
          this.viewPageData();
          break;

        case "cooperation":
          this.cooperation();
          break;

        case "delete":
          this.delete();
          break;

        case "unCooperation":
          this.unCooperation();
          break;

        case "publishTemplate":
          this.publishTemplate();
          break;
      }
    },

    // 新建页面
    newPage() {
      let newPageData = editorProjectConfig.getProjectConfig();
      this.loading = true;
      this.$API
        .createPage({ ...newPageData })
        .then(res => {
          this.loading = false;
          if (res.body) {
            this.$router.push({ name: "Editor", query: { id: res.body._id } });
          }
        })
        .catch(() => {
          this.loading = false;
        });
    },

    // 编辑
    edit() {
      this.$router.push({ name: "Editor", query: { id: this.pageData._id } });
    },

    // 复制页面
    copyPage() {
      this.loading = true;
      this.$API
        .copyPage({ id: this.pageData._id })
        .then(res => {
          this.loading = false;
          this.$router.push({ name: "Editor", query: { id: res.body._id } });
        })
        .catch(() => {
          this.loading = false;
        });
    },

    // 发布 ----
    publish() {
      this.loading = true;
      this.$API
        .publishPage({ id: this.pageData._id })
        .then(() => {
          this.loading = false;
          this.$message.success("发布成功");
          this.preview(this.pageData._id);
          this.$emit("refresh");
        })
        .catch(() => {
          this.loading = false;
        });
    },

    // 预览
    preview(id) {
      this.$emit("showPreview", id);
    },

    // 复制链接
    copyUrl() {},

    // 设为我的模板
    setTemplate() {
      this.loading = true;
      this.$API
        .setTemplatePage({ id: this.pageData._id })
        .then(() => {
          this.loading = false;
          this.$message.success("已添加到我的模板");
        })
        .catch(() => {
          this.loading = false;
        });
    },

    // 页面数据
    viewPageData() {
      this.$router.push({
        name: "pageDataDetail",
        query: { id: this.pageData._id }
      });
    },

    // 协作设置
    cooperation() {
      addCooperationer(this.pageData._id);
    },

    // 删除
    delete() {
      this.$alert("确认删除页面？删除后，将无法访问此页面?", "操作提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$API.deletePage({ id: this.pageData._id }).then(() => {
          this.$message.success("删除成功！");
          this.$emit("refresh");
        });
      });
    },

    // 退出协作
    unCooperation() {
      this.$alert("确认退出协作编辑？退出后，将无法编辑此页面?", "操作提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$API
          .delCooperation({
            pageId: this.pageData._id,
            userId: this.$store.state.user.userInfo._id
          })
          .then(() => {
            this.$message.success("已退出！");
            this.$emit("refresh");
          });
      });
    },

    // 发布模板到模板市场
    publishTemplate() {
      this.loading = true;
      this.$API
        .publishPage({ id: this.pageData._id })
        .then(() => {
          this.loading = false;
          this.$message.success("发布成功");
        })
        .catch(() => {
          this.loading = false;
        });
    }
  }
};
